Interpreter.defineErlangFunction("maps", "get", 3, (key, map, defaultValue) => {
	if (!Type.isMap(map)) {
		Interpreter.raiseBadMapError(map);
	}

	const encodedKey = Type.encodeMapKey(key);

	if (map.data[encodedKey]) {
		return map.data[encodedKey][1];
	}

	return defaultValue;
});

